.contactus {
    width: 100vw;
    background-color: darkgray;
    padding-bottom: 1rem;
}
.contactus__title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
    font-size: 25px;
    color: #9ACD32;
}
.contactus__title h1 {
    margin-top: 5rem;
}
.contactus__form-inputs {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 2rem;
}
#input , textarea {
    margin-bottom: 5px;
    line-height: 27px;
    padding: 0 1rem;
    color: #9ACD32;
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    font-weight: 700;
    outline: none;
    border-top-right-radius: 5px;
    border-bottom-left-radius: 5px;
}
#button {
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    background-color: #9ACD32;
}
#button:hover {
    box-shadow: 3px 3px 5px black;
    color: white;
    background: black;
}
#input:hover , textarea:hover {
    box-shadow: 3px 3px 5px #9ACD32;
}
@media screen and (max-width:1050px) {
    .contactus__title {
        font-size: 20px;
    }
}
@media screen and (max-width:650px) {
    .contactus__title {
        font-size: 15px;
    }
}
@media screen and (max-width:300px) {
    .contactus__title {
        font-size: 10px;
    }
    input , textarea {
        font-size: 10px;
        padding: 0 10px;
    }
}